// import * as actionTypes from './actionTypes.js'
import api from '../api/api.js'

import Store from './store.js'
import { EventBus } from '../plugins/eventbus.js'

const actions = {
    // adder ({ commit }) {
    //   commit('adder', 1)
    // },
    async getStudent ({ commit }) {
      let studentData = await api.getMe();
      await commit('getStudent', studentData)
    },
    async getAssignmentID ({ commit }) {
      let vars = window.location.search.split('=')
      await commit('getAssignmentID', vars[1])
    },
    async getAssignment ({ commit }, assignmentID) {
      let data = await api.getAssignment(assignmentID)
      commit('getAssignment', data.problemSetID )
    },
    async getProblemSetProblems ({ commit }, problemSetID) {
      let data = await api.getProblemSetProblems(problemSetID)
      commit('getProblemSetProblems', data[0].problemID)
    },
    async GetProblemStepsAction ({ commit }, pid) {
      let problems = await api.getProblemSteps(pid)
      console.log("problem steps: ", problems)
      commit("GetProblemStepsMutation", problems)
      EventBus.$emit('update-infopanel-problemsteps', Store.getters.getProblemSteps)
      console.log("lookup table: ", Store.getters.getFileLookup)
    },
    async GetAssignmentProblemCommitLastAction ({ commit }) {
      let aid = Store.getters.getAssignmentID
      let pid = Store.getters.getProblemID
      let data = await api.GetAssignmentProblemCommitLast(aid, pid)
      if (data != undefined) {
        if (data.score == 0) {
          Store.dispatch('setCurrentStep', data.step)
          // show the instruction for a step
          // EventBus.$emit('update-treevue-instructions', data.step)
        } else if (data.score == 1) {
          Store.dispatch('setCurrentStep', data.step+1)
          // show the instruction for a step
          // EventBus.$emit('update-treevue-instructions', data.step+1)
        }
        commit('GetAssignmentProblemCommitLastMutation', data.step)
      }
    },
    async GetAssignmentProblemCommitLastAllAction ({ commit }) {
      // args = [Store.getters.getAssignmentID, Store.getters.getProblemID, Store.getters.getLastCommitedStep]
      let aid = Store.getters.getAssignmentID
      let pid = Store.getters.getProblemID
      let lastCommitedStep = Store.getters.getLastCommitedStep
      // let lastCommitedStep = args[2]
      console.log("lastCommitedStep", lastCommitedStep)
      // if (lastCommitedStep >= 1) {
      let data = await api.GetAssignmentProblemCommitLastAll(aid, pid, lastCommitedStep)
      if (data != undefined) {
        console.log("data: ", data)
        commit('GetAssignmentProblemCommitLastAllMutation', [data.files])
      }
      // }
      // tell the infoPanel which step the student is on. Plays into check marks
      EventBus.$emit('update-infopanel-currentstep', Store.getters.getCurrentStep)
      // show the instruction for a step | offset by 1. [0 ... n-1]
      EventBus.$emit('update-treevue-instructions', Store.getters.getProblemSteps[Store.getters.getCurrentStep-1])
    },
    async getProblemSets ({ commit }, pid) {
      setTimeout(function () {
        api.getProblemSets(pid)
          .then(problemSets => {
            console.log("problemSets: ", problemSets)
          })
      })
    },
    selectFile ({ commit }, file) {
      commit('selectFile', file)
    },
    editFile ({ commit }, args) { // watch for 'code' changes and edit file
      commit('editFile', args)
    },
    changeInfoPanelView ({ commit }, index) {
      commit('changeInfoPanelView', index)
    },
    setCurrentStep ({ commit }, step) {
      commit('setCurrentStep', step)
    }
}; // actions

export default actions
