  const PostCommitBundlesUnsigned = (bundle) => {
    return fetch(`https://codegrinder.cs.dixie.edu/v2/commit_bundles/unsigned`, {
      method: 'POST',
      body: bundle,
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      }
    })
    .then(res => {
      return res.json()
    })
  }

  const PostCommitBundlesSigned = (bundle) => {
    return fetch(`https://codegrinder.cs.dixie.edu/v2/commit_bundles/signed`, {
      method: 'POST',
      body: bundle,
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      }
    })
    .then(res => {
      return res.json()
    })
  }

  const getProblem = (pid) => {
    return fetch('https://codegrinder.cs.dixie.edu/v2/problems/' + pid, {
      method: 'GET',
      credentials: 'include'
    }).then(res => {
      return res.json()
    })
  }

  const getMe = async () => {
      let response = await fetch("https://codegrinder.cs.dixie.edu/v2/users/me", {
        method: 'GET',
        credentials: 'include',
      })
      let json = await response.json();
      return json;
    }

  const getProblemSetProblems = async (pid) => {
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/problem_sets/${pid}/problems`, {
      method: 'GET',
      credentials: 'include',
    })
    let json = await response.json();
    return json;
  }

  const getAssignment = async (aid) => {
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/assignments/${aid}`, {
      method: 'GET',
      credentials: 'include',
    })
    let json = await response.json();
    return json;
  };

  const GetAssignmentProblemCommitLast = async (aid, pid) => {
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/assignments/${aid}/problems/${pid}/commits/last`, {
      method: 'GET',
      credentials: 'include',
    })
    if (response.status == 404) {
      console.log("no last step")
      return
    } else if (response.ok == true) {
      let json = await response.json();
      return json;
    }
  };

  const GetAssignmentProblemCommitLastAll = async (aid, pid, step) => {
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/assignments/${aid}/problems/${pid}/steps/${step}/commits/last`, {
      method: 'GET',
      credentials: 'include',
    })
    if (response.ok == true) {
      let json = await response.json();
      return json;
    }
  }

  const getProblemSets = async (pid) => {
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/problem_sets/${pid}`, {
      method: 'GET',
      credentials: 'include',
    })
    let json = await response.json();
    return json;
  }

  const getProblemSteps = async (pid) => { // problem set id?
    let response = await fetch(`https://codegrinder.cs.dixie.edu/v2/problems/${pid}/steps`, {
      method: 'GET',
      credentials: 'include',
    })
    let json = await response.json();
    return json;
  }

