<template>
  <v-app>
    <v-toolbar app>
      <v-btn v-on:click="increment"></v-btn>
      {{ count }}
    </v-toolbar>

  </v-app>
</template>

<script>
import Store from '../../store/store.js'
// import Api from '../../api/api.js'

export default {
  name: 'ToolBar',
  Store,
  methods: {
    increment: () => {
      Store.commit('increment')
    }
  },
  computed: {
    count () {
      return Store.state.count
    }
  },
}

</script>
