<template>
  <v-container>

    <v-flex d-flex>
        <v-card id="InfoPanelContainer">
          <v-card-title> {{ panel.title }} </v-card-title>
        </v-card>
        <v-layout justify-end column>

          <v-menu v-if="panel.title == 'instructions'" transition="slide-x-transition" bottom>
            <template v-slot:activator="{ on }">
              <v-btn dark icon v-on="on">
                <v-icon class="material-icons md-18">view_list</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-tile v-for="(problemStep, i) in problemSteps" :key="problemStep" @click="showInstructions(problemStep)">
                <v-icon class="resultPassed" v-if="completedStep(problemStep, i)">done</v-icon>&nbsp&nbsp<v-list-tile-title>{{ problemStep.note }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>

      </v-layout>

    </v-flex>

  </v-container>
</template>

<script>
import Store from '../../store/store.js'
import { EventBus } from '../../plugins/eventbus.js'

// const clickHandler = function() {
//   console.log(`Oh a clicked happend`)
// }
// EventBus.$on('i-got-clicked', clickHandler);
// EventBus.$on('update-panel', (btn) => {
//   console.log(`Oh a clicked happend `, btn)
// })

var tempProblemSteps = [{
    instructions: "<html><head></head><body><h1>Add two numbers</h1>↵↵<p>Write a function called <code>adder</code> that returns the sum of its two↵arguments. For example:</p>↵↵<pre><code>adder(5, 7)↵</code></pre>↵↵<p>should return 12.</p>↵</body></html>",
    note: "Add two numbers",
    step: 1,
    weight: 1,
    score: 1,
  }, {
    instructions: "<html><head></head><body><h1>Add two numbers</h1>↵↵<p>Write a function called <code>adder</code> that returns the sum of its two↵arguments. For example:</p>↵↵<pre><code>adder(5, 7)↵</code></pre>↵↵<p>should return 12.</p>↵</body></html>",
    note: "Add three numbers",
    step: 2,
    weight: 1,
    score: 0,
}]

export default {
  name: 'InfoPanel',
  Store,
  data () {
    return {
      panel: {
        title: 'files'
      },
      problemSteps: tempProblemSteps,
      currentStep: Number(1),
    }
  },
  methods: {
    // when the step in the dropdown is clicked. the treeVue gets updated to switch to that steps instructions
    showInstructions(problemStep) {
      // show the instruction for a step
      EventBus.$emit('update-treevue-instructions', problemStep)
      console.log("update instruction: ", problemStep)
    },
    // returns true when a step has been compteted. true gives a check-mark
    completedStep(problemStep, i) {
      return (i+1 < this.currentStep ? true : false)
    },
  },
  created: function() {
    EventBus.$on('update-info-panel', (panel) => {
      this.panel = panel
    })
    EventBus.$on('update-infopanel-problemsteps', (problemsteps) => {
      console.log("update-infopanel-problemsteps ", problemsteps)
      this.problemSteps = problemsteps
    })
    EventBus.$on('update-infopanel-currentstep', (currentStep) => {
      this.currentStep = currentStep
    })
  },
}

</script>

<style scoped>
.container {
    padding: 0px;
}
.navigation {
    background-color: rgb(42, 42, 42);
}
.resultPassed {
  color: #8cc63f !important;
}

</style>
