<template>
    <v-container>
        <v-btn v-for="command in commands" v-on:click="grind(command.command)" :key="command.id">{{ command.command }}</v-btn>
    </v-container>
</template>

<script>

import Store from '../../store/store.js'
import { EventBus } from '../../plugins/eventbus.js'
import api from '../../api/api.js'

const encoder = function (data) {
  let decodedString = data
  let encodedString = btoa(decodedString)
  return encodedString
}
const decodeMessage = function (data) {
  let encodedString = data
  let decodedString = atob(encodedString)
  return decodedString
}

export default {
  methods: {
    wsHandler(msg) { // problem set id?
      this.socket.send(msg)
    },
    connectSocket(host, test, action, daycareObject) {
      let url = 'wss://'+ host + '/v2/sockets/' + test + '/' + action
      console.log("url// ", url)
      this.socket = new WebSocket(url)
      this.socket.onopen = async (event) => {
        await console.log("You're now connected to the server. ")
        this.wsHandler(daycareObject)
      };
      this.socket.onmessage = (msg) => {
        var wsResponse = JSON.parse(msg.data)
        // console.log("wsResponse, ", wsResponse)
        if (wsResponse['commitBundle']) {
          console.log("commit bundle ", wsResponse['commitBundle'])
          if (wsResponse['commitBundle'].commit.reportCard.passed == false) {
            let report = wsResponse['commitBundle'].commit.reportCard.results
            let reportCardNote = wsResponse['commitBundle'].commit.reportCard.note
            console.log("report ", report)
            EventBus.$emit('push-to-terminal', '\n')
            EventBus.$emit('push-to-terminal', reportCardNote)
            EventBus.$emit('update-info-panel', { id: 3, icon: "R", title: "results" })
            EventBus.$emit('update-treeVue', { id: 3, icon: "R", title: "results" })
            EventBus.$emit('update-treevue-result', report)
          } else { // reportCard.passed == true
            let reportCardNote = wsResponse['commitBundle'].commit.reportCard.note
            EventBus.$emit('push-to-terminal', '\n')
            EventBus.$emit('push-to-terminal', reportCardNote)
            EventBus.$emit('update-treevue-result', reportCardNote)
            Store.dispatch('setCurrentStep', wsResponse['commitBundle'].commit.step+1)
            let wsResponseCommitBundle = JSON.stringify({
              hostname: wsResponse.commitBundle.hostname,
              userID: wsResponse.commitBundle.userID,
              commit: wsResponse.commitBundle.commit,
              commitSignature: wsResponse.commitBundle.commitSignature
            })
            api.PostCommitBundlesSigned(wsResponseCommitBundle) // send signed bundle back to the TA.
              .then(() => {
                EventBus.$emit('update-infopanel-currentstep', Store.getters.getCurrentStep)
                EventBus.$emit('update-treevue-instructions', Store.getters.getProblemSteps[Store.getters.getCurrentStep-1])
                EventBus.$emit('update-info-panel', { id: 2, icon: "I", title: "instructions" }) // changes view
                EventBus.$emit('update-treeVue', { id: 2, icon: "I", title: "instructions" }) // changes view
              })
          }
        // parseing through wsResponse events and sending stdout to the terminal
        } else if (wsResponse['event'].event == "stderr") {
          let stdout = decodeMessage(wsResponse['event'].streamdata)
          EventBus.$emit('push-to-terminal', stdout)
        } else if (wsResponse['event'].event == "stdout") {
          let stdout = decodeMessage(wsResponse['event'].streamdata)
          EventBus.$emit('push-to-terminal', stdout)
        } else if (wsResponse['event'].event == "exit") {
          let status = wsResponse['event'].exitstatus
          EventBus.$emit('push-to-terminal', `exit with status ${status}`)
        }
      };
    },
    grade(command) {
      EventBus.$emit('push-to-terminal', 'grind grade')
      let step = Number(Store.getters.getCurrentStep)
      let lookup = Store.getters.getFileLookup // lookup is offset by 1
      let files = Store.getters.getFiles
      let bundleFiles = {}
      files.forEach(function(file) {
        console.log("file: ", file)
        bundleFiles[file.file] = encoder(file.code)
      })
      let id = Number(Store.getters.getUserId)
      let commit = {
        action: "command",
        assignmentID: Number(-1),
        files: bundleFiles,
        problemID: Number(-1),
        step: Number(-1),
      }
      commit.action = command.toLowerCase()
      commit.assignmentID = Number(Store.getters.getAssignmentID)
      commit.problemID = Number(Store.getters.getProblemID)
      commit.step = step
      let unsignedBundle = JSON.stringify({
        commit: commit,
        userID: id
      })
      api.PostCommitBundlesUnsigned(unsignedBundle)
        .then(signedBundle => {
          var daycareObject = JSON.stringify({
            "commitBundle": signedBundle
          })
          this.connectSocket(signedBundle.hostname, signedBundle.problem.problemType, commit.action, daycareObject)
        })
    },
    grind(command) {
      switch (command) {
        case "GRADE":
          console.log("grind grade")
          this.grade(command)
          break;
        default:
      }
    }
  },
  data () {
    return {
      socket: null,
      commands: [
        { id: 1, command: "GRADE" },
        { id: 2, command: "SAVE" },
        { id: 3, command: "RUN" },
      ]
    }
  },
}

</script>

<style scoped>
.container {
    padding: 0px;
    /* background-color: rgba(63, 63, 63, 1.0); */
}
</style>
