<template>
  <v-container>
    <!-- <sl-vue-tree v-if="gotFiles" @nodeclick="fileClicked($event)" v-model="nodes"> -->
    <sl-vue-tree v-show="showFiles" @nodeclick="fileClicked($event)" @getNode="fileClicked($event)" v-model="nodes">

      <template slot="toggle" slot-scope="{ node }">
          <span v-if="!node.isLeaf">
            <icon name="caret-right" v-if="!node.isExpanded"></icon>
            <icon name="caret-down"  v-if="node.isExpanded"></icon>
          </span>
      </template>

      <template slot="title" slot-scope="{ node }">
          <icon name="file" v-if="node.isLeaf"></icon> {{ node.title }} </template>

      <template slot="sidebar" slot-scope="{ node }">
          <icon name="circle" v-if="node.data.isModified"></icon>
      </template>
    </sl-vue-tree>

    <!-- change this div to something prettier from vuetify -->
    <div id="instructionsContainer" class="probStepDescription" v-show="showInstructions" v-description="instructions"></div>

    <div class="probStepDescription" v-show="showResults">
      <v-card id="resultCards" v-on:click="showTestFile(result)" v-for="(result,i) in results" :key="i">
        <v-icon class="material-icons md-30" :class="{ 'resultPassed': checkResultPassed(result) }">done</v-icon>&nbsp&nbsp {{result.name}} {{result.outcome}}
      </v-card>
    </div>

  </v-container>
</template>

<script>
import slVueTree from 'sl-vue-tree';
import 'sl-vue-tree/dist/sl-vue-tree-dark.css';

import 'vue-awesome/icons';
import Icon from 'vue-awesome/components/Icon';

import Store from '../../store/store.js'
import { EventBus } from '../../plugins/eventbus.js'

let nodes = [
  {title: 'hello.py', isLeaf: true, code: "def main(): return true"},
  {title: 'world.py', isLeaf: true, code: "def main(): return true"},
  {title: 'python.py', isLeaf: true, code: "def main(): return true"},
  {title: 'example.py', isLeaf: true, code: "def main(): return true"},
];

export default {
  name: 'TreeVue',
  components: {
    slVueTree,
    Icon
  },
  data () {
    return {
      nodes: nodes,
      files: 'default files',
      testFiles: [],
      instructions: '',
      results: [
        {name: "unittest.loader._FailedTest -> test_6_CircleConstructor", outcome: "failed"},
        {name: "test_1_CarConstructor.Test_CarConstructor -> test_01", outcome: "passed"},
      ],
      step: Number(0),
      showFiles: true,
      showInstructions: false,
      showProblems: false,
      showResults: false,
    }
  },
  directives: {                       // this code watches for when data changes.
    'description' (el, binding) {     // this binds to the tag and sets the html to the "CURRENT" description. Current may changed when the user clicks other problems.
      el.innerHTML = binding.value    // v-description="instructions" where this div is bound to the varable 'instructions'
    },
    // 'results' (el, binding) {
    //   el.innerHTML = binding.value
    // }
  },
  methods: {
    checkResultPassed(result) {
      if (result.outcome == 'passed') {
        return true
      }
      return false
    },
    fileClicked(event) {
      console.log("event: ", event)
      EventBus.$emit('update-filepanel', event.title)
      Store.dispatch('selectFile', event.title)
    },
    showTestFile(test) {
      console.log("test ", test)
      // var a = "website.html";
      var name
      if (test.outcome == "passed") {
        name = test.name.split(".")[0]
      } else {
        name = test.name.split(/([-> ])/g).pop()
        // console.log(name)
      }

      console.log("name: ", name)
      Object.keys(this.testFiles).forEach((file) => {
        // https://regexr.com/
                    // name          code
        // console.log(file + " " + this.testFiles[file])
        // "Hello awesome, world!".split(/[\s,]+/)
        // console.log(file.split(/[.//\\]/))
        let test = file.split(/[.//\\]/)[1]
        console.log("testName: ", test)
        if (test.match(name)) {
          console.log("File: ", file)
          // send file code to treeVue
          // add another file tab with the name
          EventBus.$emit('filetabs-push-file', file)
          return
        }
      })
    }
  },
  watch: {
    nodes(val) {
      // console.log("watching: ", val)
    }
  },
  computed: {
    lastStep: function() {
      let lastStep = Store.getters.getLastCommitedStep
      this.step = lastStep
      return this.step
    },
  }, // computed
  created: function () {
    EventBus.$on('update-vuetree-files', (files) => {
      if (files) {
        let tmp = []
        files.forEach(function (file) {
          let newFile = { title: file.file, isLeaf: true, code: file.code }
          tmp.push(newFile)
        })
        this.nodes = tmp

        let testFiles = Store.getters.getProblemSteps.pop().files
        if (testFiles) {
          console.log("testFiles ", testFiles)
          this.testFiles = testFiles
        }
      };
    })
    EventBus.$on('update-treeVue-selectedFiles', (file) => {
      console.log("nodes: ", nodes)
    })
    EventBus.$on('update-treevue-instructions', (problem) => {
      this.instructions = problem.instructions
    })
    EventBus.$on('update-treevue-result', (results) => {
      this.results = results
    })
    EventBus.$on('update-treeVue', (page) => {
      if (page.title == "files") {
          this.showFiles = true
          this.showInstructions = false
          this.showResults = false
      } else if (page.title == "instructions") {
          this.showFiles = false
          this.showInstructions = true
          this.showResults = false
      } else if (page.title == "results") {
          this.showFiles = false
          this.showInstructions = false
          this.showResults = true
      }
    })

  },
}

</script>

