<template>
  <!-- bidirectional data binding（双向数据绑定） -->
  <codemirror v-model="code" :options="cmOptions">{{ watchFile }}</codemirror>

</template>

<script>
import { codemirror } from 'vue-codemirror'
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/base16-dark.css'
import 'codemirror/theme/mbo.css'
import 'codemirror/mode/python/python.js'

import Store from '../../store/store.js'
import { mapState } from 'vuex'
import { mapGetters } from 'vuex'

// for more codemirror api command visit https://codemirror.net/doc/manual.html#config
export default {
  data () {
    return {
      code: '',
      cmOptions: {
        tabSize: 2,
        indentUnit: 2,
        smartIndent: false,
        // indentWithTabs: true,
        mode: 'python',
        theme: 'mbo',
        lineNumbers: true,
        line: true,
      },
      selectedFile: "null",
    }
  },
  watch: {
    selectedFile(fileName) {
      if (Store.getters.getFiles.length >= 1) {
        let files = Store.getters.getFiles
        files.forEach((file) => {
          if (file.file == fileName) {
            this.code = file.code
            return
          }
        })
      }
    },
    code(rawText) { // watch for 'code' changes and edit file
      Store.dispatch('editFile', [rawText, this.selectedFile])
    },
  },
  components: {
      codemirror,
  },
  computed: {
    codemirror() {
      return this.$refs.myCm.codemirror
    },
    watchFile() {
      this.selectedFile = Store.state.view.selectedFile
      return this.selectedFile
    },
  },
  mounted() {
    // console.log('this is current codemirror object', this.codemirror)
    // you can use this.codemirror to do something...
  }
}
</script>

<style>
.CodeMirror {
  min-height: 100%;
  z-index: 0;
}
</style>
