import Vue from 'vue'
import Vuex from 'vuex'

import actions from './actions.js'
import mutations from './mutations.js'
import getters from './getters.js'
// import * as actionTypes from './actionTypes.js'
import api from '../api/api.js'

import { EventBus } from '../plugins/eventbus.js'

Vue.use(Vuex)

const store = new Vuex.Store({
      state: {
        theme: "dark",
        // count: 1,
        commitBundle: null,
        student: {
          id: Number(-1),
          assignmentID: Number(-1),
          lastCommitedStep: Number(1),
          currentStep: Number(1),
          name: "default",
          problemSteps: [], // list of problems for the student to solve. each problems has their description and files
                      // get the problems first and their files. then override the committed files
          problemID: Number(-1),
          problemSetID: Number(-1),
        },
        view: {
          fileLookup: [],           // lookup table for files that each problem step cares about
          files: [],                // the students files. End all be all. just under view buecause they can be viewed.
          selectedFile: Number(-1), // the selected file in treeVue
          // infoPanel: Number(1),     // the index on info panel [Files, Instructions, Results] // default is Files
        }
      }, // state
      actions,
      mutations,
      getters,
  });

  // store.dispatch('adder');
  const getStudentRequest = async () => {
    await store.dispatch('getStudent')
  };
  const getAssignmentIDRequest = async () => {
    await store.dispatch('getAssignmentID')
  };
  const getAssignmentRequest = async () => {
    await getAssignmentIDRequest()
    await store.dispatch('getAssignment', store.getters.getAssignmentID) // gets and saves problemSetID
  };
  const getProblemSetProblemsRequest = async () => {
    await getAssignmentRequest()
    await store.dispatch('getProblemSetProblems', store.getters.getProblemSetID) // will get a problemID needed for functions below
    EventBus.$emit('update-vuetree-files', store.getters.getFiles) // empty file from fresh project
  };
  const getProblemStepsRequest = async () => {
    await getProblemSetProblemsRequest()
    await store.dispatch('GetProblemStepsAction', store.getters.getProblemID)
  };
  const getAssignmentProblemCommitLastRequest = async () => {
    await getProblemStepsRequest()
    await store.dispatch('GetAssignmentProblemCommitLastAction')
  }
  const getAssignmentProblemCommitLastAllRequest = async () => {
    await getAssignmentProblemCommitLastRequest()
    await store.dispatch('GetAssignmentProblemCommitLastAllAction')
    // console.log("student files: ", store.getters.getFiles)
    EventBus.$emit('update-vuetree-files', store.getters.getFiles) // student edited files
  }

  getStudentRequest();
  getAssignmentProblemCommitLastAllRequest() // a sequence of calls that looks like "grind get ###"

  export default store

  // https://vuex.vuejs.org/guide/actions.html
  // get the assignmentid and problemid and plug that into GetAssignmentProblemCommitLastAll(aid, pid)
