// import * as actionTypes from './actionTypes.js'

const decoder = function (data) {
  let encodedString = data
  let decodedString = atob(encodedString)
  return decodedString
}

const mutations = {
  // adder: (state, number) => {
  //   state.count += number
  // },
  getStudent: (state, data) => {
    state.student.name = data.name
    state.student.id = data.id
  },
  getAssignmentID: (state, aid) => {
    state.student.aid = aid
  },
  getAssignment: (state, problemSetID) => {
    state.student.problemSetID = problemSetID
  },
  getProblemSetProblems: (state, problemID) => {
    state.student.problemID = problemID
  },
  // parses through files and saves any file without a '/'
  GetProblemStepsMutation: (state, problems) => {
    state.student.problemSteps = problems
    for ( let i = 0; i < problems.length; i++ ) { // begin parsing - parse through the files that do not have a '/'
      let files = Object.keys(problems[i].files)
      let lookup = []
      files.forEach(function(file) {              // any file w/o a '/' is added to student files
        let goodFile = true
        for (let j = 0; j < file.length; j++) {
          if (file[j] == "/") {
            goodFile = false
          }
        }
        if (goodFile == true) {
          let fileName = String(file)
          let newFile = {
            file: file,
            code: "",     // starter code has no code. could change if the professor wants go give them code in starter code. otherwise its init. to empty
            step: i+1,    // its a good idea to keep track of which step this file belongs to. steps start at 1 to n-1
          }
          state.view.files.push(newFile)///////////// end of parsing - through files with a '/'
          lookup.push(file)
        }
      })
      state.view.fileLookup.push(lookup)
    }
  },
  GetAssignmentProblemCommitLastMutation: (state, step) => {
    console.log("last step, ", step)
    state.student.lastCommitedStep = step // setting laststep to make calls for CommitLastAll
  },
  // function will look at what files are currently in the store. Overrides starter files with student commited files
  GetAssignmentProblemCommitLastAllMutation: (state, args) => { // args([commitedFile])
    let commitedFiles = args[0]
    Object.keys(commitedFiles).forEach(function(commitFile) {
      // console.log(commitFile + " " + commitedFiles[commitFile]);
      state.view.files.forEach(function(studentFile) {
        if ( commitFile == studentFile.file ) {
          studentFile.code = decoder(commitedFiles[commitFile]) // override the starter file with commited file
        }
      })

    });
  },
  setCurrentStep: (state, step) => {
    state.student.currentStep = step
  },
  selectFile: (state, file) => {
    state.view.selectedFile = file
  },
  editFile: (state, args) => { // watch for 'code' changes and edit file
    let fileName = args[1]
    let code = args[0]

    // state.view.files.indexOf(file.fileName) === 1 ? this.code = code : false
    state.view.files.forEach((file) => {
      if (file.file == fileName) {
        file.code = code
        return
      }
    })
  },
  changeInfoPanelView: (state, index) => {
    state.view.index = index
  },
}

export default mutations
