<template>
    <v-container>
        <v-card class="sidepanel" height='100%'>
            <v-layout align-start column>
                <!-- <v-btn v-for="btn in sideButtons" v-on:click="emitChangePanel(btn)" :key="btn.id">{{ btn.icon }}</v-btn> -->
                <v-btn dark icon v-on="on" v-on:click="emitChangePanel(0)">
                  <v-icon class="material-icons md-18">folder</v-icon>
                </v-btn>
                <v-btn dark icon v-on="on" v-on:click="emitChangePanel(1)">
                  <v-icon class="material-icons md-18">info</v-icon>
                </v-btn>
                <v-btn dark icon v-on="on" v-on:click="emitChangePanel(2)">
                  <v-icon class="material-icons md-18">check_box</v-icon>
                </v-btn>
            </v-layout>
