    </v-container>
</template>

<script>

import Store from '../../store/store.js'
import { EventBus } from '../../plugins/eventbus.js'

export default {
  methods: {
    // 0 files | 1 instruction | 2 results
    emitChangePanel(btn) {
      EventBus.$emit('update-info-panel', this.sideButtons[btn])
      EventBus.$emit('update-treeVue', this.sideButtons[btn])
    }
  },
  data () {
    return {
        sideButtons: [
        { id: 1, icon: "F", title: "files" },
        { id: 2, icon: "I", title: "instructions" },
        { id: 3, icon: "R", title: "results" },
      ],

    }
  },
}

</script>

