<template>
  <v-container>
    <v-tabs
      slot="extension"
      left
      v-model="active"
      slider-color="white"
      color="transparent"
    >
      <v-tab v-for="file in files"
        @click="fileTabClick(file)"
        :key="file"> {{ file }} &nbsp
        <v-icon
          @click="closeFile(file)"
          class="material-icons md-19"
        > close
        </v-icon>
      </v-tab>
    </v-tabs>
  </v-container>
</template>

<script>
import Store from '../../store/store.js'
import { EventBus } from '../../plugins/eventbus.js'

import 'vue-awesome/icons';
import Icon from 'vue-awesome/components/Icon';

export default {
  name: 'FilePanel',
  Store,
  components: {
    Icon,
  },
  directives: {
    'active' (el, binding) {
      // console.log("el ", el)
      // console.log("binind ", binding)
      // el.style.backgroundColor = "#555";
    },
  },
  methods: {
    fileTabClick(file) {
      Store.dispatch('selectFile', file)
      EventBus.$emit('update-treeVue-selectedFiles', file)
    },
    closeFile(file) {
      let index = this.files.indexOf(file)
      this.files.splice(index, 1)

    }
  },
  computed: {

  },
  created: function() {
    EventBus.$on('update-filepanel', (file) => {
      if (this.files.length == 0) {
        this.files.push(file)
        this.active = 0
      } else {
        this.files.indexOf(file) === -1 ? (this.files.push(file), this.active = this.files.length-1) : false
      }
      let index = this.files.indexOf(file)
      this.active = index
    })
    EventBus.$on('filetabs-push-file', (file) => {
      if (this.files.length == 0) {
        this.files.push(file)
        this.active = 0
      }
    })
  },
  data () {
    return {
      files: [],
      showclose: false,
      active: null,
      closeBtn: {
        backgroundColor: null,
      }
    }
  },
}

</script>

<style scoped>
.container {
    padding: 0px;
}
.navigation {
    background-color: rgb(42, 42, 42);
}

</style>

<style>
.v-tabs__item {
  text-transform: none;
  font-size: 15px;
}
.material-icons.md-19 {
  font-size: 19px;
}
.close_file {
  background-color: red;
}
</style>
